html,
body {
  height: 100%;
  font-family: "Roboto", normal;
}
main {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 400px;
  margin: auto;
  border: 1px solid #0d0335;
}
.head {
  display: flex;
  justify-content: center;
  background: #0d0335;
  width: 100%;
}
form {
  display: flex;
  flex-direction: column;
  padding: 2%;
  border-radius: 5px;
}
input {
  padding: 0 22px 0 5px;
  width: 100%;
}
li {
  word-wrap: break-word;
  margin-bottom: 7px;
}
#filter {
  width: 40%;
  margin: 1rem 0;
}
.column {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
h1 {
  color: rgb(255, 254, 242);
  font-size: 25px;
}
ol {
  overflow-y: scroll;
  margin: 0;
}
.completed {
  text-decoration: line-through;
}
.buttonPerformed {
  width: 18px;
  height: 18px;
  float: right;
  margin-right: 3px;
}
.buttonPerformed:hover {
  filter: invert(100%);
}
.button {
  border-radius: 5px;
  color: white;
  width: 25px;
  height: 25px;
  background-color: #0d0335;
  font-size: 20px;
  margin: 2px;
}
.btnClear {
  float: right;
  margin: 0 1rem;
}
li:hover {
  background-color: #c1c4cf;
  cursor: pointer;
}
.hide {
  display: none;
}
.completed {
  color: #949493;
}

.delete {
  position: relative;
  float: right;
  right: 3px;
  width: 20px;
  height: 20px;
  opacity: 0.3;
}
.delete:hover {
  opacity: 1;
}
.delete:before,
.delete:after {
  position: absolute;
  right: 8px;
  content: " ";
  height: 20px;
  width: 2px;
  background-color: rgb(41, 41, 41);
}
.delete:before {
  transform: rotate(45deg);
}
.delete:after {
  transform: rotate(-45deg);
}

.clear:before,
.clear:after {
  top: 3px;
  background-color: rgb(0, 0, 0);
  height: 13px;
}

.clear {
  position: relative;
  left: -26px;
  top: 1px;
  width: 27px;
  height: 20px;
}

@media screen and (max-width: 980px) {
  main {
    min-width: 300px;
    width: 95%;
    height: 95%;
    font-size: 18px;
  }
  input {
    height: 30px;
    font-size: 17px;
  }
  #add{
      width: 40px;
      height: 35px;
  }
  #filter{
      width: 45%;
      height: 30px;
      font-size: 17px;
  }
  h1{
      font-size: 30px;
  }
  .delete:before,
  .delete:after {
      width: 4px;
      height: 25px;
      margin: 3px;
  }
  div.delete{
    width: 30px;
    height: 25px;
    top: -5px;
  }
  .clear{
    left: -23px;
    top: -4px;
  }
  .buttonPerformed{
    width: 23px;
    height: 23px;
  }
}
